export const  reply =async(ctx,next)=> {
	let message = ctx.weixin
	console.log(message)
	let reply = 'sorry';
	var date = manba().format("ll");
	let username = message.FromUserName;
	console.log(username)
	let person = await client.getUser(username);
	console.log('')
	console.log(person)
	let usercode = person.name;
	console.log(usercode)
	if (message.MsgType === 'event') {
		if (message.Event === "subscribe") {
			reply = tip
			client.sendMessgaebyText(message.FromUserName, reply);
		} else if (message.Event === 'click') {
		
			let data = null;
			let params ={
				typeCode:'0',
				page:1,
				rows:10
			}
			let title =''
			if (message.EventKey === 'menu_click_business') {
				params['Classification'] =''
				params["typeCode"] ='2'
			} else if (message.EventKey === 'menu_click_design') {
				params['Classification'] =''
				title =''
				params["typeCode"] ='1'
			}else if(message.EventKey==='menu_click_Ech'){
				params["HasMark"] =true
				title =''
				params["typeCode"] ='3'
			}
			//console.log(params)
			const result =await api.project.ProjectList(usercode,params)
		    console.log(result)
			if(result.rows.length>0){
				 let sendMsg =result.rows[0]
				 let projectStatus =sendMsg.HandleStatus
				 console.log(projectStatus)
				 let status =null
				 switch(projectStatus){
					 case 0 :
					 status  =''
					 break;
					 case 1 :
					  status  =''
					  break;
					 case 2 :
					  status  =''
					  break;
				 }
				 let card ={
					 title:':'+title,
					 description: "<div class=\"gray\">:"+sd.format(new Date(), 'YYYY-MM-DD')+"</div> <div class=\"normal\">:"+
					 sendMsg.Classification+ "</div><div class=\"highlight\">:"+sendMsg.ProjectName+"</div><div class=\"highlight\">:"+status+"</div>",
					 url:`${wechat_config.SITE_ROOT_URL }/detail?ID=${sendMsg.ID}`
				 }
				 client.sendMessgaebycard(message.FromUserName,card)
			}else {
				let msg =''
				client.sendMessgaebyText(message.FromUserName, msg);
			}

		}else if(message.Event ==='view') {
			//
		}

	} else if (message.MsgType === 'text') {
		var content = message.Content;
		reply = `${content},.....`;
		if (content === "1") {
			reply = ``;
		} else if (content === "2") {
			reply = `Hi, `;
		} else if (content === "") {
			reply = `7000`;
		} else if(content.indexOf('')!==-1){
			let searchText =content.substr(3)
			console.log(searchText);
			let query ={
				searchText:searchText,
				page:'1',
				rows:10
			}
			//console.log(query)
			const projects =await api.project.Search(usercode,query)
			//console.log(projects)
			const project =projects.rows[0]
		    if(projects.rows.length>0){
				let card ={
					title:"",
					description: "<div class=\"gray\">:"+sd.format(new Date(), 'YYYY-MM-DD')+"</div> <div class=\"normal\">:"+
					project.Classification+ "</div><div class=\"highlight\">:"+project.ProjectName+"</div>",
					url:`${wechat_config.SITE_ROOT_URL }/detail?ID=${project.ID}`
				}
				client.sendMessgaebycard(message.FromUserName,card)
				return ;
			}else {
			   reply =`${searchText},<a href="${wechat_config.SITE_ROOT_URL}/project/commit"></a>`	
			}
		}else {
			reply = tip
		}
	    client.sendMessgaebyText(message.FromUserName, reply);
	} else if (message.MsgType === 'voice') {
	  //,
	//   const voiceText = message.Recognition
	//   console.log(voiceText)
	}
 }




 
